"use client";

import Link from "next/link";
import { User, FileText, Briefcase, LogOut } from "lucide-react";
import { authClient } from "@/lib/auth-client";
import { useRouter } from "next/navigation";
import { useEffect } from "react";
import { toast } from "sonner";

export default function AdminLayout({
    children,
}: {
    children: React.ReactNode;
}) {
    const router = useRouter();
    const { data: session, isPending, error } = authClient.useSession();

    // Debug logging
    useEffect(() => {
        console.log('[Admin Layout] Session state:', { session, isPending, error, hasUser: !!session?.user });
    }, [session, isPending, error]);

    useEffect(() => {
        // Only redirect if we're done loading and definitely have no user
        // Add a small delay to allow session to fully hydrate
        if (!isPending && !session?.user) {
            const timer = setTimeout(() => {
                // Double-check before redirecting
                console.log('[Admin Layout] No session after delay, redirecting...');
                router.push('/auth');
            }, 500);
            return () => clearTimeout(timer);
        }
    }, [session, isPending, router]);

    // Show loading while pending OR while we don't have user data yet (briefly)
    if (isPending) return <div className="h-screen flex items-center justify-center bg-black text-gray-400">Loading...</div>;

    if (!session?.user) {
        // Show loading briefly while waiting for redirect or session
        return <div className="h-screen flex items-center justify-center bg-black text-gray-400">Checking authentication...</div>;
    }

    const handleLogout = async () => {
        await authClient.signOut({ fetchOptions: { credentials: "include" } });
        toast.success("Logged out successfully", {
            description: "Session cleared. See you next time!"
        });
        router.push('/auth');
    };

    return (
        <div className="flex h-screen bg-black text-white">
            {/* Sidebar */}
            <aside className="w-64 bg-dark border-r border-white/10 flex flex-col">
                <div className="h-16 flex items-center px-6 border-b border-white/10">
                    <span className="font-black text-xl tracking-tighter text-white">ADMIN</span>
                </div>

                <nav className="flex-1 p-4 space-y-2">
                    <Link href="/admin" className="flex items-center gap-3 px-4 py-3 rounded-lg text-gray-400 hover:bg-white/5 hover:text-primary transition-colors">
                        <User size={20} />
                        <span className="font-bold text-sm">Dashboard</span>
                    </Link>
                    <Link href="/admin/blogs" className="flex items-center gap-3 px-4 py-3 rounded-lg text-gray-400 hover:bg-white/5 hover:text-primary transition-colors">
                        <FileText size={20} />
                        <span className="font-bold text-sm">Blogs</span>
                    </Link>
                    <Link href="/admin/projects" className="flex items-center gap-3 px-4 py-3 rounded-lg text-gray-400 hover:bg-white/5 hover:text-primary transition-colors">
                        <Briefcase size={20} />
                        <span className="font-bold text-sm">Portfolio</span>
                    </Link>
                </nav>

                <div className="p-4 border-t border-white/10">
                    <button onClick={handleLogout} className="flex items-center gap-3 px-4 py-3 rounded-lg text-red-500 hover:bg-red-500/10 w-full transition-colors">
                        <LogOut size={20} />
                        <span className="font-bold text-sm">Logout</span>
                    </button>
                </div>
            </aside>

            {/* Main Content */}
            <main className="flex-1 overflow-auto bg-black">
                <header className="h-16 bg-dark/50 backdrop-blur border-b border-white/10 px-8 flex items-center justify-between sticky top-0 z-50">
                    <h2 className="font-bold text-lg">Dashboard</h2>
                    <div className="w-8 h-8 rounded-full bg-primary text-dark flex items-center justify-center font-bold">A</div>
                </header>
                <div className="p-8">
                    {children}
                </div>
            </main>
        </div>
    );
}
